<template>
  <div class="user-management">
    <el-row>
      <!-- 添加用户按钮 -->
      <el-col :span="24">
        <el-button type="primary" @click="openAddUserDialog">添加子账号</el-button>
      </el-col>
    </el-row>

    <!-- 用户列表 -->
    <el-table :data="users" style="width: 100%" border>
      <el-table-column label="用户名" prop="username"></el-table-column>
      <el-table-column label="角色" prop="role"></el-table-column>
      <el-table-column label="权限" prop="permissions"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="small" @click="editUser(scope.row)">编辑</el-button>
          <el-button size="small" type="danger" @click="deleteUser(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- 添加/编辑用户弹框 -->
    <el-dialog :visible.sync="dialogVisible" title="添加/编辑用户">
      <el-form :model="userForm" label-width="120px">
        <el-form-item label="用户名" :rules="[{ required: true, message: '请输入用户名', trigger: 'blur' }]">
          <el-input v-model="userForm.username"></el-input>
        </el-form-item>
        <el-form-item label="角色" :rules="[{ required: true, message: '请选择角色', trigger: 'blur' }]">
          <el-select v-model="userForm.role" placeholder="请选择角色">
            <el-option label="主账号" value="admin"></el-option>
            <el-option label="子账号" value="user"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="权限" :rules="[{ required: true, message: '请选择权限', trigger: 'blur' }]">
          <el-checkbox-group v-model="userForm.permissions">
            <el-checkbox label="查看权限" name="permissions">查看权限</el-checkbox>
            <el-checkbox label="编辑权限" name="permissions">编辑权限</el-checkbox>
            <el-checkbox label="删除权限" name="permissions">删除权限</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="saveUser">保存</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false, // 控制弹框显示
      users: [
        { username: 'admin', role: 'admin', permissions: ['查看权限', '编辑权限', '删除权限'] },
        { username: 'user1', role: 'user', permissions: ['查看权限'] },
        { username: 'user2', role: 'user', permissions: ['查看权限', '编辑权限'] }
      ],
      userForm: {
        username: '',
        role: 'user',
        permissions: []
      }
    };
  },
  methods: {
    // 打开添加子账号弹框
    openAddUserDialog() {
      this.userForm = { username: '', role: 'user', permissions: [] };
      this.dialogVisible = true;
    },
    // 编辑子账号
    editUser(user) {
      this.userForm = { ...user };
      this.dialogVisible = true;
    },
    // 删除子账号
    deleteUser(user) {
      this.users = this.users.filter(u => u.username !== user.username);
    },
    // 保存用户（添加或编辑）
    saveUser() {
      if (this.userForm.username && this.userForm.role && this.userForm.permissions.length > 0) {
        if (!this.users.find(u => u.username === this.userForm.username)) {
          this.users.push(this.userForm);
        } else {
          const index = this.users.findIndex(u => u.username === this.userForm.username);
          this.users[index] = this.userForm;
        }
        this.dialogVisible = false;
      } else {
        this.$message.error('请填写完整的用户信息');
      }
    }
  }
};
</script>

<style scoped>
.user-management {
  padding: 20px;
}

.el-table {
  margin-top: 20px;
}
</style>
