import Vue from 'vue'
import Router from 'vue-router'
import HomeView from '../views/HomeView.vue'
import LoginPage from '../views/LoginPage.vue'
import ProductCalculation from '../views/ProductCalculation.vue'
import BatchOpenASIN from '../views/BatchOpenASIN.vue'
import Page3 from '../views/Page3.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: LoginPage,
    },
    {
      path: '/home',
      name: 'Home',
      component: HomeView,
      meta: { requiresAuth: true }, // 需要登录才能访问
      children: [
        {
          path: '', // 默认子路由
          component: BatchOpenASIN, // 设置默认加载的子组件
        },
        {
          path: 'ProductCalculation',
          component: ProductCalculation,
        },
        {
          path: 'BatchOpenASIN',
          component: BatchOpenASIN,
        },
        {
          path: 'page3',
          component: Page3,
        },
      ],
    },
    {
      path: '*', // 捕获所有未知路由
      redirect: '/login', // 默认跳转到登录页
    },
  ],
})

// 全局前置守卫
router.beforeEach((to, from, next) => {
  const savedData = localStorage.getItem('loginData') // 获取登录数据

  // 检查是否保存了登录信息
  if (savedData) {
    const loginData = JSON.parse(savedData)
    const currentTime = new Date().getTime() // 获取当前时间

    // 如果登录信息未过期
    if (currentTime < loginData.expiration) {
      // 如果目标路由需要登录并且用户未登录，重定向到登录页面
      if (
        to.matched.some((record) => record.meta.requiresAuth) &&
        !loginData.username
      ) {
        next({ name: 'Login' })
      } else {
        next() // 继续正常的路由跳转
      }
    } else {
      // 登录信息过期，清除登录数据
      localStorage.removeItem('loginData')
      // 如果当前访问的是登录页，直接进入，否则跳转到登录页
      if (to.name === 'Login') {
        next()
      } else {
        next({ name: 'Login' })
      }
    }
  } else {
    // 如果没有保存登录信息，且访问需要认证的页面，则重定向到登录页面
    if (to.matched.some((record) => record.meta.requiresAuth)) {
      next({ name: 'Login' })
    } else {
      next() // 没有登录信息，直接进入页面
    }
  }
})

export default router
