<template>
  <div id="app">
    <!-- <router-link to="/login">Go to Login</router-link> -->
    <router-view></router-view>
    <!-- 路由视图展示 -->
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'App',
  data () {
    return {
      message: ''
    }
  },
  mounted () {
    // 请求 Express 后端的数据
    if (window.location.protocol !== 'file:') {
      axios
        .get('http://localhost:3000/api/data')
        .then(response => {
          this.message = response.data.message
        })
        .catch(error => {
          console.error('There was an error!', error)
        })
    } else {
      console.log('Running in local file mode, skipping API request.')
      this.message = 'Local mode: no API request'
    }
  }
}
</script>

