<template>
  <div id="home">
    <el-row style="height: 100%; margin: 0;">
      <!-- 上半部分 -->
      <el-col :span="24" style="padding: 0; height: 5%;">
        <el-row style="height: 100%; margin: 0;">
          <!-- 上面左容器 -->
          <el-col :span="2" style="padding: 0;">
            <div class="box">首页</div>
          </el-col>
          <!-- 上面右容器 -->
          <el-col :span="22" style="padding: 0;">
            <div class="box">
              <el-button type="danger" @click="handleLogout">退出登录</el-button>
            </div>
          </el-col>
        </el-row>
      </el-col>

      <!-- 下半部分 -->
      <el-col :span="24" style="padding: 0; height: 95%;">
        <el-row style="height: 100%; margin: 0;">
          <!-- 下面左容器 (放置导航栏)-->
          <el-col :span="2" style="padding: 0;">
            <div class="box">
              <!-- 导航栏 -->
              <el-menu
                :default-active="activeMenu"
                class="el-menu-vertical-demo"
                @select="handleMenuSelect"
                style="height: 100%"
              >
                <el-menu-item index="1" @click="navigateTo('ProductCalculation')">产品核算</el-menu-item>
                <el-menu-item index="2" @click="navigateTo('BatchOpenASIN')">网址批量打开</el-menu-item>
                <el-menu-item index="3" v-if="isAdmin" @click="navigateTo('UserManagement')">用户管理</el-menu-item>
                <el-menu-item index="4" @click="navigateTo('Settings')">设置</el-menu-item>
              </el-menu>
            </div>
          </el-col>

          <!-- 下面右容器 (根据路由显示不同页面)-->
          <el-col :span="22" style="padding: 0;">
            <div class="box">
              <!-- 动态路由视图 -->
              <router-view></router-view>
            </div>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'HomeView',
  data() {
    return {
      activeMenu: '', // 默认选中的菜单项
      isAdmin: false // 判断是否是管理员
    };
  },
  created() {
    // 获取登录用户信息
    const loginData = JSON.parse(localStorage.getItem('loginData'));
    if (loginData && loginData.role === 'admin') {
      this.isAdmin = true; // 如果是管理员，设置 isAdmin 为 true
    }
  },
  methods: {
    // 跳转到对应的页面
    navigateTo(page) {
      const currentPath = this.$route.path; // 获取当前的路由路径
      const targetPath = `/home/${page}`; // 目标路径

      // 如果当前路径和目标路径相同，则不做任何操作
      if (currentPath === targetPath) return;

      this.$router.push(targetPath); // 否则执行路由跳转
      this.activeMenu = page === 'ProductCalculation' 
                        ? '1' 
                        : page === 'BatchOpenASIN' 
                          ? '2' 
                          : '3'; // 设置活动菜单项
    },

    // 处理菜单选中事件
    handleMenuSelect(index) {
      this.activeMenu = index;
    },

    // 退出登录功能
    handleLogout() {
      localStorage.removeItem('loginData'); // 清除登录信息
      this.$store.dispatch('updateLoginSuccess', false); // 更新 Vuex 状态

      // 跳转到登录页面
      this.$router.push('/login');
      this.$message.success('已成功退出登录');
    }
  }
};
</script>

