import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loginSuccess: false, // 管理登录状态
  },
  mutations: {
    // 修改 loginSuccess 状态
    setLoginSuccess(state, status) {
      state.loginSuccess = status
    },
  },
  actions: {
    // 提交 mutation 来改变 loginSuccess 状态
    updateLoginSuccess({ commit }, status) {
      commit('setLoginSuccess', status)
    },
  },
})
