<template>
  <div id="login">
    <el-form :model="form" @submit.native.prevent="handleLogin">
      <el-form-item label="用户名">
        <el-input v-model="form.username" placeholder="请输入用户名"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input
          type="password"
          v-model="form.password"
          placeholder="请输入密码"
        ></el-input>
      </el-form-item>
      <el-button
        type="primary"
        :disabled="!form.username || !form.password"
        @click="handleLogin"
      >
        登录
      </el-button>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'LoginPage',
  data () {
    return {
      form: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    handleLogin () {
      // 判断用户名和密码是否为空
      if (!this.form.username || !this.form.password) {
        this.$message.error('用户名和密码不能为空')
        return
      }

      // 验证用户名和密码是否正确
      if (
        this.form.username === 'admin666888' &&
        this.form.password === 'lyl1688.'
      ) {
        // 主账号登录成功，存储角色信息
        this.$store.dispatch('updateLoginSuccess', true)

        // 存储角色信息到 localStorage
        const loginData = {
          username: this.form.username,
          role: 'admin', // 主账号角色
          expiration: new Date().getTime() + 3 * 24 * 60 * 60 * 1000 // 3天有效期
        }
        localStorage.setItem('loginData', JSON.stringify(loginData))

        // 显示登录成功消息
        this.$message.success('恭喜你，主账号登录成功！')
        this.$router.push('/home')
      } else if (
        this.form.username === 'user' &&
        this.form.password === 'user123'
      ) {
        // 子账号登录成功，存储角色信息
        this.$store.dispatch('updateLoginSuccess', true)

        // 存储角色信息到 localStorage
        const loginData = {
          username: this.form.username,
          role: 'user', // 子账号角色
          expiration: new Date().getTime() + 3 * 24 * 60 * 60 * 1000 // 3天有效期
        }
        localStorage.setItem('loginData', JSON.stringify(loginData))

        // 显示登录成功消息
        this.$message.success('欢迎，子账号登录成功！')
        this.$router.push('/home')
      } else {
        this.$message.error('用户名或密码错误')
      }
    }
  },
  mounted () {
    // 检查是否有保存的登录信息
    const savedData = localStorage.getItem('loginData')
    if (savedData) {
      const loginData = JSON.parse(savedData)
      const currentTime = new Date().getTime()

      // 如果过期时间小于当前时间，清除登录数据
      if (currentTime > loginData.expiration) {
        localStorage.removeItem('loginData')
        this.$message.error('登录信息已过期，请重新登录')
        this.$router.push('/login') // 重定向到登录页面
      } else {
        // 如果没有过期，直接跳转到主页
        this.$router.push('/home')
      }
    }
  }
}
</script>

<style scoped>
#login {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
}
</style>
