<template>
  <div class="product-calculation">
    <el-row class="form-container" :gutter="20">
      <el-col :span="24">
        <el-form :model="form" ref="form" label-width="120px">
          <!-- 采购价格 -->
          <el-form-item
            label="采购价格 (元)"
            :rules="[
              { required: true, message: '请输入采购价格', trigger: 'blur' }
            ]"
          >
            <el-input
              v-model="form.purchasePrice"
              placeholder="请输入采购价格"
              @input="validateDecimal('purchasePrice')"
              size="medium"
            />
          </el-form-item>

          <!-- 产品重量 -->
          <el-form-item
            label="产品重量 (g)"
            :rules="[
              { required: true, message: '请输入产品重量', trigger: 'blur' }
            ]"
          >
            <el-input
              v-model="form.weight"
              placeholder="请输入产品重量 (g)"
              @input="validateDecimal('weight')"
              size="medium"
            />
          </el-form-item>

          <!-- 销售价格 -->
          <el-form-item
            label="销售价 (美元)"
            :rules="[
              { required: true, message: '请输入销售价格', trigger: 'blur' }
            ]"
          >
            <el-input
              v-model="form.salesPrice"
              placeholder="请输入销售价格"
              @input="validateDecimal('salesPrice')"
              size="medium"
            />
          </el-form-item>

          <!-- 产品包装尺寸 -->
          <el-form-item
            label="包装尺寸"
            :rules="[
              { required: true, message: '请输入包装尺寸', trigger: 'blur' }
            ]"
          >
            <el-row :gutter="0">
              <el-col :span="8">
                <el-input
                  v-model="form.length"
                  placeholder="长 (cm)"
                  @input="validateDecimal('length')"
                  size="medium"
                />
              </el-col>
              <el-col :span="8">
                <el-input
                  v-model="form.width"
                  placeholder="宽 (cm)"
                  @input="validateDecimal('width')"
                  size="medium"
                />
              </el-col>
              <el-col :span="8">
                <el-input
                  v-model="form.height"
                  placeholder="高 (cm)"
                  @input="validateDecimal('height')"
                  size="medium"
                />
              </el-col>
            </el-row>
          </el-form-item>

          <!-- 计算按钮 -->
          <el-button
            type="primary"
            :disabled="!isFormValid"
            @click="calculateProfit"
            size="medium"
            style="width: 100%; margin-bottom: 10px"
          >
            计算利润
          </el-button>
        </el-form>
      </el-col>
    </el-row>

    <!-- 一键清除按钮 -->
    <el-button
      type="danger"
      @click="clearForm"
      icon="el-icon-delete"
      size="small"
      style="position: absolute; top: -50px; right: 20px; padding: 6px 10px"
    >
      一键清除
    </el-button>

    <div class="permissions" v-if="isAdmin">
      <!-- 文件上传 -->
      <div class="upload-demo">
        <el-upload
          drag
          action="#"
          :on-change="handleFileChange"
          :file-list="fileList"
          :before-upload="beforeUpload"
          :on-remove="handleRemove"
          accept=".csv, .xls, .xlsx"
          style="width: 100%"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">点击或拖拽上传文件</div>
        </el-upload>
      </div>

      <!-- 上传成功提示 -->
      <div v-if="tableData.length !== 0" class="display-container">
        {{ TableName }}
        <!-- 关闭按钮 -->
        <!-- 关闭按钮 (图标) -->
        <el-button
          style="
            position: absolute;
            right: 40px;
            top: 15px;
            padding: 0;
            height: 20px;
            font-size: 20px;
            color: #ff0000;
          "
          type="text"
          @click="clearUpload"
        >
          <i class="el-icon-close" style="font-size: 20px"></i>
        </el-button>
      </div>
      <div class="export-box">
        <!-- 导出按钮 -->
        <el-button
          :type="tableData.length === 0 ? 'primary' : 'success'"
          @click="exportData"
          size="medium"
          :style="{
            marginTop: '20px',
            cursor: tableData.length === 0 ? 'not-allowed' : 'pointer'
          }"
          :disabled="tableData.length === 0 || isExporting"
        >
          导出数据
        </el-button>

        <!-- 下载模板按钮 -->
        <el-button
          type="info"
          @click="downloadTemplate"
          size="medium"
          style="margin-top: 20px"
        >
          下载模板
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import * as XLSX from 'xlsx'

export default {
  data () {
    return {
      form: {
        purchasePrice: null, // 采购价格（人民币）
        weight: null, // 产品重量（g）
        salesPrice: null, // 销售价格（美元）
        length: null, // 包装尺寸（长 cm）
        width: null, // 包装尺寸（宽 cm）
        height: null // 包装尺寸（高 cm）
      },
      fileList: [],
      isExporting: false, // 标记是否正在导出
      tableData: [],
      profit: null,
      profitMargin: null,
      isQualified: false,
      TableName: '',
      isAdmin: false // 判断是否是管理员
    }
  },
  created () {
    // 获取登录用户信息
    const loginData = JSON.parse(localStorage.getItem('loginData'))
    if (loginData && loginData.role === 'admin') {
      this.isAdmin = true // 如果是管理员，设置 isAdmin 为 true
    }
  },
  computed: {
    // 计算表单是否有效，所有字段必须填写且合法
    isFormValid () {
      return (
        this.form.purchasePrice &&
        this.form.weight &&
        this.form.salesPrice &&
        this.form.length &&
        this.form.width &&
        this.form.height
      )
    }
  },
  methods: {
    clearUpload () {
      this.tableData = [] // 清空表格数据
      this.TableName = '' // 清空文件名
    },
    calculateProfit () {
      const exchangeRate = 7.1 // 汇率

      // 获取表单中的数据
      const salesPriceUSD = parseFloat(this.form.salesPrice) // 销售价（美元）
      const purchasePriceCNY = parseFloat(this.form.purchasePrice) // 采购价（人民币）
      const weightInGrams = parseFloat(this.form.weight) // 产品重量（克）
      const weightInKg = weightInGrams / 1000 // 转换为公斤
      const length = parseFloat(this.form.length) // 包装尺寸（长 cm）
      const width = parseFloat(this.form.width) // 包装尺寸（宽 cm）
      const height = parseFloat(this.form.height) // 包装尺寸（高 cm）

      // 计算包装重量：长 * 宽 * 高 / 6000
      const packagingWeight = (length * width * height) / 6000

      // 头程物流费用：使用最大的重量（产品重量或包装重量）
      const weightToUseForLogistics = Math.max(weightInKg, packagingWeight) // 选择较大的重量

      // 计算头程物流费用（20元/kg）
      const headLogisticsCost = 20 * weightToUseForLogistics // 20元/kg

      // 计算售后费用（销售价的10%）
      const afterSaleCostCNY = salesPriceUSD * exchangeRate * 0.1

      // 计算店铺佣金（销售价的15%）
      const commissionCostCNY = salesPriceUSD * exchangeRate * 0.15

      // 计算尾程物流费用（2美元）
      const finalLogisticsCostCNY = 2 * exchangeRate

      // 计算总成本
      const totalCost =
        purchasePriceCNY +
        headLogisticsCost +
        afterSaleCostCNY +
        commissionCostCNY +
        finalLogisticsCostCNY

      // 计算销售总额（人民币）
      const totalSalesInCNY = salesPriceUSD * exchangeRate

      // 计算利润
      this.profit = totalSalesInCNY - totalCost

      // 计算毛利率
      this.profitMargin = ((this.profit / totalSalesInCNY) * 100).toFixed(2)

      // 判断是否合格，并展示不同的信息
      if (this.profitMargin < 15) {
        this.isQualified = false
        this.$alert('不通过', '核算结果', {
          type: 'error', // 红色的错误类型
          center: true, // Center the content
          showClose: false, // Remove the close button
          dangerouslyUseHTMLString: true, // Allow HTML for custom styling
          customClass: 'centered-alert' // Custom class for additional styling
        })
      } else if (this.profitMargin >= 15 && this.profitMargin <= 22) {
        this.isQualified = false
        this.$alert('需要审核', '核算结果', {
          type: 'warning', // 黄色的警告类型
          center: true, // Center the content
          showClose: false, // Remove the close button
          dangerouslyUseHTMLString: true, // Allow HTML for custom styling
          customClass: 'centered-alert' // Custom class for additional styling
        })
      } else {
        this.isQualified = true
        this.$alert('通过', '核算结果', {
          type: 'success', // 绿色的成功类型
          center: true, // Center the content
          showClose: false, // Remove the close button
          dangerouslyUseHTMLString: true, // Allow HTML for custom styling
          customClass: 'centered-alert' // Custom class for additional styling
        })
      }
      console.log(this.profitMargin)
    },
    handleFileChange (file) {
      const reader = new FileReader()
      reader.onload = e => {
        const data = e.target.result
        const workbook = XLSX.read(data, { type: 'binary' })

        // 获取工作表名称
        const sheetName = workbook.SheetNames[0]
        const worksheet = workbook.Sheets[sheetName]

        // 获取表头
        const headers = XLSX.utils.sheet_to_json(worksheet, { header: 1 })[0]
        // 检查表头是否符合预期
        const requiredHeaders = [
          'SKU',
          '采购价格（元）',
          '重量（g）',
          '售价（美元）',
          '长（cm）',
          '宽（cm）',
          '高（cm）'
        ]
        for (const header of requiredHeaders) {
          if (!headers.includes(header)) {
            this.$message.error('表头错误，请确保表格列名与模板一致')
            return
          }
        }

        // 解析数据
        const jsonData = XLSX.utils.sheet_to_json(worksheet)
        for (let index = 0; index < jsonData.length; index++) {
          const item = jsonData[index]

          // 先检查字段是否包含字母或中文
          const fields = [
            '采购价格（元）',
            '售价（美元）',
            '重量（g）',
            '长（cm）',
            '宽（cm）',
            '高（cm）'
          ]
          let hasInvalidChars = false

          fields.forEach(field => {
            if (/[a-zA-Z\u4e00-\u9fa5]/.test(item[field])) {
              hasInvalidChars = true
            }
          })

          // 如果有无效字符，停止下一步并提示用户
          if (hasInvalidChars) {
            this.$message.error('表格内列包含字母或中文，数据无效。')
            return // 停止后续代码执行
          }
        }
        // 遍历解析后的数据并确保数字格式
        this.tableData = jsonData.map(item => {
          // 转换为数字，保留两位小数
          const purchasePrice = this.toNumber(item['采购价格（元）'])
          const salesPrice = this.toNumber(item['售价（美元）'])
          const weight = this.toNumber(item['重量（g）'])
          const length = this.toNumber(item['长（cm）'])
          const width = this.toNumber(item['宽（cm）'])
          const height = this.toNumber(item['高（cm）'])

          // 计算利润和毛利率
          const profit = this.TableCalculation(
            purchasePrice,
            salesPrice,
            weight,
            length,
            width,
            height
          )
          const profitMargin = ((profit / (salesPrice * 7.1)) * 100).toFixed(2)

          // 判断是否通过审核
          return {
            ...item,
            profit: profit.toFixed(2),
            profitMargin: profitMargin + '%',
            status:
              profitMargin >= 15 && profitMargin < 22
                ? '需要审核'
                : profitMargin >= 22
                ? '通过'
                : '不通过'
          }
        })
      }
      this.TableName = file.name
      reader.readAsBinaryString(file.raw)
      // 文件无误，显示上传成功
      this.$message.success('上传成功！')
    },

    beforeUpload (file) {
      const isExcel = /\.(csv|xls|xlsx)$/.test(file.name)
      if (!isExcel) {
        this.$message.error('只能上传 CSV 或 Excel 文件')
      }
      return isExcel
    },
    handleRemove (file, fileList) {
      this.fileList = fileList
    },

    TableCalculation (purchasePrice, salesPrice, weight, length, width, height) {
      const exchangeRate = 7.1 // 汇率
      const weightInKg = weight / 1000 // 转换为公斤
      const packagingWeight = (length * width * height) / 6000 // 计算包装重量
      const weightToUseForLogistics = Math.max(weightInKg, packagingWeight)
      const headLogisticsCost = 20 * weightToUseForLogistics // 头程物流费用
      const afterSaleCost = salesPrice * exchangeRate * 0.1
      const commissionCost = salesPrice * exchangeRate * 0.15
      const finalLogisticsCost = 2 * exchangeRate
      const totalCost =
        purchasePrice +
        headLogisticsCost +
        afterSaleCost +
        commissionCost +
        finalLogisticsCost
      const totalSalesInCNY = salesPrice * exchangeRate
      const profit = totalSalesInCNY - totalCost
      return profit
    },

    exportData () {
      this.isExporting = true // 设置标记，开始导出
      const ws = XLSX.utils.json_to_sheet(this.tableData)
      const wb = XLSX.utils.book_new()
      XLSX.utils.book_append_sheet(wb, ws, '核算结果')
      XLSX.writeFile(wb, 'product_calculation_results.xlsx')
    },

    downloadTemplate () {
      const templateData = [
        {
          SKU: '',
          '采购价格（元）': '',
          '重量（g）': '',
          '售价（美元）': '',
          '长（cm）': '',
          '宽（cm）': '',
          '高（cm）': ''
        }
      ]

      const ws = XLSX.utils.json_to_sheet(templateData)
      const wb = XLSX.utils.book_new()
      XLSX.utils.book_append_sheet(wb, ws, '模板')
      XLSX.writeFile(wb, 'product_calculation_template.xlsx')
    },

    clearForm () {
      this.form = {
        purchasePrice: null,
        weight: null,
        salesPrice: null,
        length: null,
        width: null,
        height: null
      }
      this.profit = null
      this.profitMargin = null
      this.isQualified = false
    },

    toNumber (value) {
      return isNaN(value) || value === '' ? 0 : parseFloat(value)
    }
  }
}
</script>

