<template>
  <div class="BatchOpenASIN">
    <div class="box1">
      <h1>选择站点</h1>
    </div>
    <div class="box1">
      <div class="custom-radio" v-for="(label, code) in amazonSites" :key="code">
        <input
          type="radio"
          :id="code"
          name="amazonSite"
          :value="code"
          v-model="selectedSite"
        />
        <label :for="code">{{ label }}</label>
      </div>
    </div>

    <div class="box1">
      <h1>输入多个ASIN</h1>
    </div>

    <div class="custom-input">
      <textarea
        v-model="asinInput"
        rows="4"
        cols="50"
        placeholder="在此输入多个ASIN，每行一个"
      ></textarea>
    </div>

    <div class="box1">
      <button class="custom-button" @click="openAmazonProducts">
        批量打开ASIN
      </button>
    </div>

    <div class="long">
      <p>● 如果无法自动打开：请 关闭浏览器 “弹出窗口拦截” 功能，才能正常使用。</p>
      <p>例如：360安全浏览器8：打开菜单：选项/设置 => 高级设置 => 取消【不允许任何网站显示弹出式窗口】前面的勾，或添加本站到例外网站</p>
      <p>● 浏览器须支持Javascript。</p>
      <p>● 推荐使用多标签的浏览器，如 搜狗浏览器、Maxthon、火狐浏览器 或 Chrome。</p>
      <p>● 同时打开网址数请勿过多，以免超出浏览器限制，一般为20-30，或引起浏览器崩溃死机。</p>
      <p>● 如页面异常，请 Ctrl + F5 强制刷新浏览器！</p>
      <p>● 网址的长度不受限制，双击输入框，或选中输入框，Ctrl+A 可全选其中内容！</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectedSite: 'com', // 默认选中的站点
      asinInput: '', // 用户输入的ASIN列表
      amazonSites: {
        de: '德国 (Amazon.de)',
        fr: '法国 (Amazon.fr)',
        es: '西班牙 (Amazon.es)',
        it: '意大利 (Amazon.it)',
        com: '美国 (Amazon.com)',
        'co.uk': '英国 (Amazon.co.uk)'
      }
    };
  },
  methods: {
    openAmazonProducts() {
      const asins = this.asinInput
        .split('\n')
        .map((asin) => asin.trim())
        .filter((asin) => asin); // 清除空的ASIN

      const amazonUrls = asins.map((asin) => `https://www.amazon.${this.selectedSite}/dp/${asin}`);

      // 通过window.open批量打开链接
      amazonUrls.forEach((url) => {
        window.open(url, '_blank'); // 打开每个链接在新标签页
      });
    }
  }
};
</script>
